import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

import ModuleUI from './ui'
import ModuleCatalog from './catalog'

const store = new Vuex.Store({
	modules: {
		ui: ModuleUI,
		catalog: ModuleCatalog
	}
})

import ModuleServer from './server'
if (process.env.VUE_APP_SERVER == 'local')
	store.registerModule('server', ModuleServer)

export default store