<template lang="pug">

.popup.product
	.name {{ product.name }}
	.price {{ product.price }}
	.sum {{ product.price * product.count }}

</template>
<script>
export default {
	computed: {
		product() {
			return this.$store.getters['catalog/product'](this.id)
		},
	},
	props: {
		id: {
			type: Number,
			required: true
		}
	}
}
</script>
<style lang="stylus" scoped>

.popup.product
	font-size 20px
	color #000000
	height auto
	width auto
	margin 0
	transform translate(-50%, -50%)

</style>