import Axios from 'axios'
const serverURL = 'http://localhost:3000'

export default {
	namespaced: true,
	state: () => ({
		products: {
			show: [],
			cart: [],
			all: []
		},
		categories: [],
		server: {
			category: 0,
			sort: {
				name: 'popularity',
				direction: -1
			},
			search: '',
			sum: 0,
			loading: true,
			offset: 0,
			loaded: false
		}
	}),
	getters: {
		cart: ({ products }) => products.cart,
		show: ({ products }) => products.show,
		sum: ({ server }) => Math.abs(server.sum.toFixed(2)),
		cartCount: ({ products }) => products.cart.length,
		loaded: ({ server }) => server.loaded,
		loading: ({ server }) => server.loading,
		product: ({ products: { all } }) => (id) => all.find(item => item.id == id),
		
		categoryActive: ({ categories, server }) => categories.find(item => item.id == server.category) || null,

		categoryChildren: ({ categories }) => (category) => {
			return category ?
				categories.filter(item => item.path == `${category.path}${category.id}.`) :
				categories.filter(item => item.path == '0.')
		},

		category: ({ categories }) => (id) => categories.find(item => item.id == id),

		categoryParent: ({ categories }) => (category) => {
			let path = category.path.split('.')
			return categories.find(item => item.id == path[path.length - 2]) || null
		},
	},
	mutations: {

		load({ products: { all, show }, categories, server }, { products: prods, categories: cats }) {
			all.push(...prods)
			show.push(...prods)
			categories.push(...cats)
			server.loaded = true
		},

		setShownProducts({ products: { all, show } }, products) {
			show.splice(0)
			for (let product of products){
				const loaded = all.find(item => item.id == product.id)
				if (loaded)
					show.push(loaded)
				else {
					all.push(product)
					show.push(product)
				}
			}
		},

		setProductCount({ products, server }, { product, count }) {
			count = count > 0 ? count : 0

			server.sum -= product.count * product.price
			product.count = count
			server.sum += product.count * product.price

			if (!product.cart && count > 0) {
				product.cart = true
				products.cart.push(product)
			}
		},

		removeProductFromCart({ products, server }, product) {
			server.sum -= product.count * product.price
			product.count = 0
			product.unit = 0
			product.cart = false
			products.cart.splice(products.cart.findIndex(item => item.id == product.id), 1)
		},

		setLoading({ server }, value) {
			server.loading = value
		},

		setSort({ server }, { name, direction }) {
			server.sort.name = name
			server.sort.direction = direction
		},

		setSearch({ server }, text) {
			server.search = text
		},

		setCategory({ server }, id) {
			server.category = id
		},

	},
	actions: {

		async load({ commit, dispatch }) {
			commit('setLoading', true)

			if (process.env.VUE_APP_SERVER == 'server') {
				const { data } = await Axios.get(serverURL + '/api/preload')
				console.log(JSON.stringify(data.products))
				console.log(JSON.stringify(data.categories))
				commit('load', data)
			}
			if (process.env.VUE_APP_SERVER == 'local')
				dispatch('server/load', null, { root: true })

			commit('setLoading', false)
		},

		async setFilter({ commit, dispatch, state: { server } }, { name, payload }) {
			commit('setLoading', true)
			commit(name, payload)

			const { data: products } = await Axios.post(serverURL + '/api/filter', {
				sort: server.sort,
				search: server.search.length > 0 ? server.search : null,
				category: server.category > 0 ? server.category : null,
				offset: server.offset > 0 ? server.offset : null
			})
			if (process.env.VUE_APP_SERVER == 'server')
				commit('setShownProducts', products)
			if (process.env.VUE_APP_SERVER == 'local')
				dispatch('server/setShownProducts', null, { root: true })

			commit('setLoading', false)
		},

		setLoading({ commit }, value) {
			commit('setLoading', value)
		},

	}
}