import productsDB from 'data/products.json'
import categoriesDB from 'data/categories.json'
const DB = { products: productsDB, categories: categoriesDB }

export default {
	namespaced: true,
	actions: {
		load({ commit, dispatch }) {
			const { products } = DB

			for (let product of products)
				product.count = 0

			commit('catalog/load', DB, { root: true })
			dispatch('setShownProducts')
		},
		setShownProducts({
			rootState: { catalog: {
				products, categories, server: { sort, search, category }
			}}}) {
			let show

			if (category == 0)
				show = products.all.filter(item => (search == '' || item.name.toLowerCase().includes(search.toLowerCase(), -1)))
			else {
				let categoriesList = categories.filter(item => item.path.includes(`.${category}.`) || item.id == category)
				show = products.all.filter(item =>
					(
						search == '' ||
						item.name.toLowerCase().includes(search.toLowerCase(), -1)
					) &&
					(
						categoriesList.find(category => category.id == item.category)
					)
				)
			}

			show = show.sort((a, b) => a[sort.name] > b[sort.name] ? 1 * sort.direction : -1 * sort.direction)

			products.show = show
		},
	}
}