<template lang="pug">

transition(name="show" :duration="{ enter: 800, leave: 550 }")
	.popup-container(v-if="popup.name")
		.fade(@click="popup = {}")
		v-popup-login(v-if="popup.name == 'login'")
		v-popup-product(v-if="popup.name == 'product'" :id="popup.query")

</template>

<script>
import vPopupLogin from './login'
import vPopupProduct from './product'
export default {
	computed:{
		popup:{
			get() {
				return this.$store.getters['ui/popup']
			},
			set({ name = null, query = null }){
				this.$store.dispatch('ui/setPopup', { name, query })
			}
		},
	},
	components: {
		vPopupLogin,
		vPopupProduct
	},
}

</script>

<style lang="stylus" scoped>

$fade-opacity = .5

.popup
	z-index 2
	position absolute
	width 400px
	height 500px
	top -100%
	top -100vh
	left 50%
	left 50vw
	transform translate(-50%, -50%)
	background-color #fff
	box-shadow 0 0 10px 0 rgba(#000, .2)
	border-radius 5px
	top 50%
	top 50vh
	&-container
		position fixed
		top 0
		left 0
		width 100%
		height 100%
		width 100vw
		height 100vh
		z-index 200
		.fade
			position absolute
			width 100%
			height 100%
			top 0
			left 0
			z-index 1
			background-color #000
			cursor pointer
			opacity $fade-opacity
		&.show-enter-active
			.popup
				animation showPopup .8s
			.fade
				animation fadeIn .8s
		&.show-leave-active
			.popup
				animation hidePopup .6s
			.fade
				animation fadeOut .6s
	

@keyframes fadeOut
	0%
		opacity $fade-opacity
	100%
		opacity 0
@keyframes fadeIn
	0%
		opacity 0
	100%
		opacity $fade-opacity
@keyframes hidePopup
	0%
		margin-top 0
		top 50%
		top 50vh
	30%
		margin-top 35px
		top 50%
		top 50vh
	100%
		margin-top 0
		top -100%
		top -100vh
@keyframes showPopup
	0%
		margin-top 0
		top -100%
		top -100vh
	60%
		margin-top 45px
		top 50%
		top 50vh
	100%
		margin-top 0
		top 50%
		top 50vh

</style>