<template lang="pug">

.popup.login
	form(@submit.prevent="send")
		input.tel(type="tel" v-model="tel" :class="{ invalid: validation.tel === false, valid: validation.tel === true }" @input="inputTel")
		input.password(type="password" v-model="password" :class="{ invalid: validation.password === false, valid: validation.password === true }" @input="inputPassword")
		button.submit 

</template>
<script>
export default {
	data: () => ({
		tel: '',
		password: '',
		validation: {
			tel: null,
			password: null
		}
	}),
	methods: {
		send() {
			this.$store.dispatch('ui/login', {
				tel: this.tel,
				password: this.password
			})
		},
		inputTel() {
			console.log(this.tel)
		},
		inputPassword() {
			console.log(this.password)
		},
	}
}
</script>
<style lang="stylus" scoped>

.popup.login
	height auto
	padding 20px
	form
		display flex
		flex-direction column
	input
		font-size 16px
		margin-bottom 10px
	.submit
		font-size 16px

</style>