<template lang="pug">

.header
	.left
		router-link.logo(:to="{ name: 'home' }") central shop
		.nav
			router-link.nav-link(v-for="link in links" :key="link.url" :to="{ name: link.url }") {{ link.name }}
	.right
		.login(@click="openLogin") 

</template>
<script>
export default {
	data: () => ({
		links: [
			{
				name: '',
				url: 'home'
			},
			{
				name: '',
				url: 'catalog'
			},
			{
				name: ' ',
				url: 'about'
			},
			{
				name: '',
				url: 'contacts'
			},
			{
				name: '',
				url: 'documentation'
			},
		]
	}),
	methods: {
		openLogin() {
			this.$store.dispatch('ui/setPopup', { name: 'login'})
		}
	},
}
</script>
<style lang="stylus" scoped>

.header
	display flex
	justify-content space-between
	align-items center
	position fixed
	top 30px
	left 0
	width 100%
	padding 20px 40px
	z-index 10

.left, .right
	display flex
	align-items center

.logo
	font-size 20px
	font-weight 900
	text-transform uppercase
	margin-right 40px
	color #444

.nav
	display flex
	&-link
		font-size 15px
		color $yellow
		margin-right 30px
		&.router-link-exact-active
			color $deep-green
			font-weight 500

.login
	font-size 15px
	color $yellow
	margin-right 20px
	cursor pointer

</style>