<template lang="pug">

.first-screen
	v-flickity.slider(ref="flickity" :options="sliderOptions")
		.slide
			.slide-inner
		.slide
			.slide-inner
		.slide
			.slide-inner

</template>
<script>
import vFlickity from 'vue-flickity'
export default {
	data () {
		return {
			sliderOptions: {
				wrapAround: true,
				prevNextButtons: false,
				autoPlay: 5000,
				pauseAutoPlayOnHover: true
			}
		}
	},
	components: {
		vFlickity
	}
}
</script>
<style lang="stylus">

.first-screen .slider
	.flickity-page-dots
		bottom -28px
		.dot
			background-color $yellow
			opacity 1
			width 12px
			height 12px
			box-shadow 0 0 2px 0 rgba(#000, .3)
			transition bakground-color .2s, box-shadow .2s
			&:hover
				box-shadow 0 0 2px 0 rgba(#000, .1)
			&.is-selected
				background-color $green
	

</style>
<style lang="stylus" scoped>

.first-screen
	padding-bottom 50px

.slider
	.slide
		width 100%
		padding 5px 0
		&-inner
			margin auto
			border-radius 20px
			width calc(100% - 80px)
			height calc(100vh - 170px)
			background-color $light-green
			box-shadow 0 1px 2px 0 rgba(#000, .1)
		&:nth-child(2)
			.slide-inner
				background-color $light-brown-grey
		&:nth-child(3)
			.slide-inner
				background-color $brown-grey


</style>