import Axios from 'axios'
const serverURL = 'http://localhost:3000'

export default {
	namespaced: true,
	state: () => ({
		popup: {
			name: null,
			query: null
		}
	}),
	getters: {
		popup: ({ popup }) => popup
	},
	mutations: {
		setPopup({ popup }, { name, query }) {
			popup.name = name || null
			popup.query = query || null
		}
	},
	actions: {
		setPopup({ commit }, data) {
			commit('setPopup', data)
		},
		async login({ dispatch }, data) {
			const { data: auth } = await Axios.post(serverURL + '/api/login', data)
			console.log(auth)
		}
	}
}