import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import store from 'core/store'

import _404 from 'temp/404'
import Home from 'temp/home'

const router = new VueRouter({
	// mode: 'history',
	routes: [
		{
			path: '*',
			name: '404',
			component: _404
		},
		{
			path: '/',
			name: 'home',
			component: Home
		},
		{
			path: '/catalog',
			name: 'catalog',
			component: () => import('temp/catalog')
		},
		{
			path: '/about',
			name: 'about',
		},
		{
			path: '/contacts',
			name: 'contacts',
		},
		{
			path: '/documentation',
			name: 'documentation',
		},
		{
			path: '/cart',
			name: 'cart',
		},
		{
			path: '/login',
			name: 'login',
		},
	],
	scrollBehavior(to, from, savedPosition) {
		if (savedPosition) {
			return savedPosition
		} else {
			return { x: 0, y: 0 }
		}
	}

})

export default router
