<template lang="pug">

#app
	v-header

	router-view.page

	.footer(v-if="!isCatalog")

	v-popup

</template>

<script>
import 'styles/base.styl'

import vHeader from 'comp/header'
import vPopup from 'comp/popup'

export default {
	computed: {
		isCatalog(){
			return this.$store.state.route.name == 'catalog'
		}
	},
	components: {
		vHeader,
		vPopup
	}
}
</script>

<style lang="stylus" scoped>

#app
	position relative
	padding-top 110px

.page
	position relative

</style>